*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body::-webkit-scrollbar{
  width: 12px;
}
body::-webkit-scrollbar-thumb{
  background-color: blueviolet;
  border-radius: 6px;
}
body::-webkit-scrollbar-track:hover {
  background-color: #ddd; 
}
body::-webkit-scrollbar-thumb:hover {
  background-color: #555; 
}
/* header styles 
*/
nav{
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  padding:  10px;
  background-color: #2c3e50;
}
nav ul{
float: right;
margin: 20px;
}
nav ul li{
  display: inline-block;
  line-height: 80px;
  margin: 0 5px;
}
 nav ul li a{
  margin-right: 15px;
  text-decoration: none;
  color: #fff;
  padding: auto;
  position: relative;
}
.checkbtn{
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}
#check{
  display: none;
}
header nav ul li ul {
  display: none;
  position: absolute;
  background-color: yellowgreen;
  list-style: none;
  padding: 0;
  margin-top: 10px;
  border-raius: 5px;
}
header nav ul li:hover ul {
  display: block;
  font-size: 1em;
}

header nav ul li ul li {
  padding: 10px;
}

header nav ul li ul li a {
  color: white;
  text-decoration: none;
}

header nav ul li ul li a:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
header nav ul li a:hover{
  cursor: pointer;
}
header nav ul li a::after{
  content: '';
  position: absolute;
  background-color: blue;
  height: 3px;
  left: 0;
  width: 0;
  bottom: -10px;
  transition: 0.3s;
}
header nav ul li a:hover::after{
  width: 100%;

}
header nav ul li{
  list-style: none;
  font-size: 1.2em;
  text-transform: capitalize;
}
.logo{
  text-decoration: none;
  text-transform: capitalize;
  font-size: 2.5em;
  background-image: linear-gradient(to right, yellow, red); 
  padding: 10px;
  border-radius: 5px;
  -webkit-background-clip: text; 
  background-clip: text;
  color: transparent;
}
.logo span{
  opacity: 0;
  transform: rotate(-180deg) translate(150px, 0);
  animation:  animate 3s forwards;
  display: inline-block;
  background-image: linear-gradient(to right, yellow, red);
  border-radius: 5px;
  -webkit-background-clip: text; 
  background-clip: text;
  color: transparent;
}
.logo span:nth-of-type(1){
  animation-delay: 1s;
}
.logo span:nth-of-type(2){
  animation-delay: 2s;
}.logo span:nth-of-type(3){
  animation-delay: 3s;
}.logo span:nth-of-type(4){
  animation-delay: 4s;
}.logo span:nth-of-type(5){
  animation-delay: 5s;
}.logo span:nth-of-type(6){
  animation-delay: 6s;
}.logo span:nth-of-type(7){
  animation-delay: 7s;
}.logo span:nth-of-type(8){
  animation-delay: 8s;
}.logo span:nth-of-type(9){
  animation-delay: 9s;
}.logo span:nth-of-type(10){
  animation-delay: 10s;
}.logo span:nth-of-type(11){
  animation-delay: 11s;
}.logo span:nth-of-type(12){
  animation-delay: 12s;
}.logo span:nth-of-type(13){
  animation-delay: 13s;
}.logo span:nth-of-type(14){
  animation-delay: 14s;
}.logo span:nth-of-type(15){
  animation-delay: 15s;
}
@keyframes animate {
  30%{
    transform: translate(0, -50px) rotate(180deg) scale(1);
  }
  50%{
    transform: translate(0, 20px) rotate(0deg) scale(1);
    opacity: 1;
  }
  80%{
    transform: translate(-100px, -100px) rotate(-180deg) scale(1);
    opacity: 0;
  }
  100%{
    transform: translate(0) rotate(0deg) scale(1);
    opacity: 1;
  }
}
